<template>
    <v-app class="grey lighten-4">
        <v-container fill-height>
            <v-layout row wrap align-center justify-center>
                <v-flex xl7 lg7 md7 sm12 xs12>
                    <div class="animated fadeIn mt-5 mb-5">
                        <loading :active.sync="isLoading" :can-cancel="false" :is-full-page="true"></loading>
                        <v-card shaped>
                            <v-card-title class="headline light-green darken-1 white--text" primary-title>
                                <h4 class="font-weight-medium text-uppercase text-right">Reporte de Soporte</h4>
                            </v-card-title>
                            <v-form ref="form" @submit.prevent="generateReporte" class="ms-3 mb-3">
                                <v-card-text>
                                    <h3 class="text-uppercase mt-2">Rango de Fechas:</h3>
                                    <v-divider></v-divider>
                                    <v-layout>
                                        <v-flex xl6 lg6 md6 xs6 class="mx-2">
                                            <v-menu v-model="datePicker.menuFechaIni" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="290px">
                                                <template v-slot:activator="{ on }">
                                                    <v-text-field v-model="report.fechaInicial" label="Seleccion Fecha Inicial" prepend-icon="fa-calendar-alt" readonly v-on="on" :rules="rules.date"></v-text-field>
                                                </template>
                                                <v-date-picker v-model="report.fechaInicial" @input="datePicker.menuFechaIni = false"></v-date-picker>
                                            </v-menu>
                                        </v-flex>
                                        <v-flex xl6 lg6 md6 xs6 class="mx-2">
                                            <v-menu v-model="datePicker.menuFechaFin" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="290px">
                                                <template v-slot:activator="{ on }">
                                                    <v-text-field v-model="report.fechaFinal" label="Seleccion Fecha Final" prepend-icon="fa-calendar-alt" readonly v-on="on" :rules="rules.date"></v-text-field>
                                                </template>
                                                <v-date-picker v-model="report.fechaFinal" @input="datePicker.menuFechaFin = false"></v-date-picker>
                                            </v-menu>
                                        </v-flex>
                                    </v-layout>
                                    <h3 class="text-uppercase mt-2">Tipo de Reporte:</h3>
                                    <v-divider></v-divider>
                                    <v-layout>
                                        <v-flex xl12 lg12 md12 xs12>
                                            <v-select label="Seleccion" :items="dataForm.data.dataFormReport.consultas" v-model="report.typeCons" :rules="rules.select"></v-select>
                                        </v-flex>
                                    </v-layout>
                                    <v-layout class="mt-2">
                                        <v-flex xl12 lg12 md12 xs12>
                                            <div class="custom-control custom-checkbox">
                                                <input type="checkbox" class="custom-control-input" id="defaultUnchecked" v-model="report.typeReport">
                                                <label class="custom-control-label font-weight-bold" for="defaultUnchecked">Generar Excel</label>
                                            </div>
                                        </v-flex>
                                    </v-layout>
                                    <v-layout>
                                        <v-flex xl12 lg12 md12 xs12 class="mt-5 animated fadeIn" v-show="resServer.view">
                                            <v-alert :type="resServer.type">
                                                {{resServer.message}}
                                            </v-alert>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                                <v-card-actions>
                                    <v-btn type="submit" class="orange lighten-1 text-white">Generar</v-btn>
                                    <v-btn class="light-green darken-1 text-white">Cancelar</v-btn>
                                    <v-btn class="red text-white" @click="closeSession">
                                        <v-icon>fa-door-open</v-icon>
                                    </v-btn>
                                </v-card-actions>
                            </v-form>
                            <v-expand-transition>
                                <div v-show="showChart">
                                    <v-divider></v-divider>
                                    <v-layout>
                                        <v-flex xl12 lg12 md12 sm12 xs12>
                                            <div id="divChart" class="mt-2 mb-2">

                                            </div>
                                        </v-flex>
                                    </v-layout>
                                </div>
                            </v-expand-transition>  
                        </v-card>
                    </div>
                </v-flex>
            </v-layout>
        </v-container>
    </v-app>
</template>
<script>
    import Vue from "vue";
    import gql from "graphql-tag";
    import query from "../query/reporte";
    import DRAW_CHART from "../charts/chart";
    import EXCEL from "../xlsx/excel";
    import Loading from "vue-loading-overlay";
    import 'vue-loading-overlay/dist/vue-loading.css';
    import swal from "sweetalert2";
    import getRules from "../validations/rules";

    Vue.use(Loading);

    export default {
        data() {
            return {
                report: {
                    fechaInicial: "",
                    fechaFinal: "",
                    typeCons: "",
                    typeReport: false,
                },
                datePicker: {
                    menuFechaIni: false,
                    modalFechaIni: false,
                    menuFechaFin: false,
                    modalFechaFin: false,
                },
                dataForm: {},
                isLoading: true,
                resServer: {
                    view: false,
                    type: "info",
                    message: ""
                },
                rules: getRules.rulesMain,
                showChart: false,
            }
        },
        components: {
            Loading,
        },
        async beforeMount() {
            await this.getTypeReportes();
        },
        methods: {
            async getTypeReportes() {
                try {
                    this.dataForm = await this.$apollo.query({
                        query: gql`${query.DATA_REPORTES}`
                    });

                    setInterval(() => {
                        this.isLoading = false;
                    }, 1000);
                } catch(e) {
                    this.$router.push("error");
                }
            },
            
            async generateReporte() {
                if (this.$refs.form.validate()) {
                    if (this.report.fechaInicial < this.report.fechaFinal) {
                        try {
                            this.isLoading = true;
                            this.resServer.view = false;

                            const reporte = await this.$apollo.query({
                                query: gql`${query.NEW_REPORT}`,
                                variables: {
                                    input: {
                                        fInicial: this.report.fechaInicial,
                                        fFinal: this.report.fechaFinal,
                                        typeSoporte: this.report.typeCons
                                    }
                                }
                            });

                             setInterval(() => {
                                this.isLoading = false;
                            }, 6000);
                            
                            if (reporte.data.reporte.length != 0) {
                                if (!this.report.typeReport) {
                                    const COLORS = DRAW_CHART.colorChart(reporte.data.reporte.length);
                                    DRAW_CHART.newChart(reporte, COLORS);
                                    this.showChart = true;
                                } else {
                                    EXCEL.newExcel(reporte.data.reporte); 
                                }

                                this.resetData();
                            } else {
                                this.resServer.view = true;
                                this.resServer.message = "Peticin sin resultados, por favor intente con otros valores";
                                this.resServer.type = "info";
                            }
                        } catch(e) {
                            setInterval(() => {
                                this.isLoading = false;
                            }, 6000);

                            this.resServer.view = true;
                            this.resServer.message = "Algo raro paso por aqu!!!";
                            this.resServer.type = "error";
                        }
                    } else {
                        this.resServer.view = true;
                        this.resServer.message = "Rango de fechas invalido, por favor intente con otros valores";
                        this.resServer.type = "error";
                    }
                    
                }
            },

            resetData() {
                this.report.fechaInicial = "";
                this.report.fechaFinal = "";
                this.report.typeCons = "";
                this.report.typeReport = false;
                this.$refs.form.reset();
            },

            closeSession() {
                swal.fire({
                    title: 'Esta Seguro???',
                    text: "Salir de la aplicacin",
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: 'Si, salir!!!'
                    }).then((result) => {
                        if (result.value) {
                            this.$session.destroy();
                            localStorage.clear();
                            location.href = "/";    
                        }
                    })
            }
        }
    }
</script>