<template>
    <v-app class="grey lighten-4">
        <v-container fill-height justify-center >
            <v-layout row wrap align-center justify-center>
                <v-flex xl7 lg7 md6 sm12 xs12>        
                    <div class="animated fadeIn">
                    <loading :active="isLoading" :can-cancel="false" :is-full-page="true"></loading>
                        <v-form ref="form" @submit.prevent="registrarSoporte" autocomplete="off">
                            <v-card shaped>
                                <v-card-title class="headline light-green darken-1 white--text" primary-title>
                                    <h4 class="font-weight-medium text-uppercase">Nuevo Soporte</h4>
                                </v-card-title>
                                <v-card-text>
                                    <v-select label="Tipo de Servicio" class="mx-5 my-4" :items="dataForm.servicios" @change="getLabores" v-model="soporte.servicio" :rules="rules.select"></v-select>
                                    <v-select label="Tcnico" class="mx-5 my-4" :items="dataForm.tecnicos" v-model="soporte.tecnico" :rules="rules.select"></v-select>
                                    <v-text-field label="Correo Institucional" class="mx-5 my-4" v-model="soporte.correo" :rules="rules.fieldCorreo" required></v-text-field>
                                    <v-select label="Perfil del Solicitante" class="mx-5 my-4" :items="dataForm.perfiles" v-model="soporte.perfil" :rules="rules.select"></v-select>
                                    <v-select label="Bloque" class="mx-5 my-4" :items="dataForm.bloques" v-model="soporte.bloque" :rules="rules.select" @change="setRulesByBloque"></v-select>
                                    <v-text-field label="Numero del Saln" class="mx-5 my-4" v-model="soporte.salon" :rules="rules.fieldSalon"></v-text-field>
                                    <v-select @change="getCambios" label="Labor Realizada" class="mx-5 my-4" :loading="isLoadingLabores" :items="labores" v-model="soporte.labor" :disabled="statusLabores" :rules="rules.select"></v-select>
                                    <template v-if="isShowCambios">
                                        <v-select label="Cambio Realizado" class="mx-5 my-4 animated fadeIn" v-model="soporte.cambio" :items="cambios" :rules="rules.select"></v-select>
                                    </template>
                                    <template v-if="statusIncidente">
                                        <v-select label="Incidente Soluciondo" class="mx-5 my-4" :items="dataForm.soluciones" v-model="soporte.solucionado" :rules="rules.select"></v-select>
                                    </template>
                                </v-card-text>
                                <v-card-actions>
                                    <v-btn type="submit" class="orange lighten-1 text-white">Registrar</v-btn>
                                    <v-btn class="light-green darken-1 my-4 text-white">Cancelar</v-btn>
                                    <v-spacer></v-spacer>
                                    <v-btn icon @click="show = !show">
                                        <v-icon>{{show ? 'fa-chevron-up' : 'fa-chevron-down'}}</v-icon>
                                    </v-btn>
                                </v-card-actions>
                                <div class="card-footer text-muted text-center">
                                    <h5 class="font-weight-bolder">Eres Admin? Ingresa <a href="/#/session">Aqu!!!</a></h5>
                                </div>
                            </v-card>
                        </v-form>
                    </div>
                </v-flex>
            </v-layout>
        </v-container>
    </v-app>
</template>
<script>
import gql from 'graphql-tag'
import Vue from 'vue';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import swal from "sweetalert2";
import query from "../query/registro";
import VueSession from "vue-session";
import getRules from "../validations/rules";

Vue.use(Loading);
Vue.use(VueSession);

export default {
    data() {
        return {
            dataForm: {
                servicios: [],
                tecnicos: [],
                perfiles: [],
                bloques: [],
                soluciones: []
            },
            labores: [],
            cambios: [],
            isLoading: true,
            isLoadingLabores: false,
            isShowCambios: false,
            statusLabores: true,
            statusIncidente: false,
            
            soporte: {
                servicio: "",
                tecnico: "",
                correo: "",
                perfil: "",
                bloque: "",
                salon: "",
                labor: "",
                cambio: null,
                solucionado: "",
            },
            rules: getRules.rulesSoporte,
            show: false
        }
    },
    components: {
        Loading
    },
    async beforeMount() {
        await this.getDataForm();
    },
    methods: {
        async getDataForm() {
            try {
                const {data} = await this.$apollo.query({
                    query: gql`${query.DATA_FORM}`
                });

                this.dataForm.servicios = data.dataForm.servicios;
                this.dataForm.tecnicos = data.dataForm.tecnicos;
                this.dataForm.perfiles = data.dataForm.perfiles;
                this.dataForm.bloques = data.dataForm.bloques;
                this.dataForm.soluciones = data.dataForm.soluciones;

                setInterval(() => {
                    this.isLoading = false;
                }, 1000);
            } catch(e) {
                this.$router.push("error");
            }
        },

        async getLabores() {
            if (this.soporte.servicio === "Soporte" || this.soporte.servicio === "Mantenimiento") {
                try {
                    this.isLoadingLabores = true;
                    
                    const listaLabores = await this.$apollo.query({
                        query: gql`${query.LABORES}`,
                        variables: {
                            typeSuport: this.soporte.servicio
                        }
                    });
                    
                    this.labores = listaLabores.data.labores.labores;
                    this.statusLabores = false;

                    if (this.soporte.servicio === "Soporte") this.statusIncidente = true;
                    else this.statusIncidente = false;

                    setInterval(() => {
                        this.isLoadingLabores = false;
                    }, 1000);
                } catch(e) {
                    await this.createAlert("Oh oh!!!", "Algo raro paso por aqu!!!", "error");
                }
            }
            else {
                this.registrarIncidente();
            }
        },

        async getCambios() {
            if (this.soporte.labor === "Vandalismo") {
                this.isShowCambios = true;

                try {
                    const getCambios = await this.$apollo.query({
                        query: gql`${query.CAMBIOS}`
                    });

                    this.cambios = getCambios.data.dataForm.cambios;
                    
                } catch(e) {
                    this.$router.push("error");
                }
            } else {
                this.isShowCambios = false;
            }
        },

        setRulesByBloque() {
            if (this.soporte.bloque === "Bloque L") {
                this.rules.fieldSalon = [
                    v => !!v || "El campo es requerido",
                ]
            } else {
                this.rules.fieldSalon = [
                    v => !!v || "El campo es requerido",
                    v => (v.length > 2) || "Minimo 3 caracteres",
                    v => (v.length < 5) || "Maximo 4 caracteres"
                ]
            }
        },

        async registrarIncidente() {
            const {value: text} = await swal.fire({
                type: "warning",
                title: "Nuevo Incidente",
                text: `${this.soporte.servicio}`,
                input: 'textarea',
                inputPlaceholder: 'Registre el incidente....',
                inputAttributes: {
                    'aria-label': 'Registre el incidente....'
                },
                confirmButtonText: "Enviar",
                showCancelButton: true,
                inputValidator: (value) => {
                    return !value && 'Por favor registre algo!!!'
                },
            })

            if (text) {
                try {
                    this.isLoading = true;
                    const nuevoCorreo = await this.$apollo.mutate({
                        mutation: gql`${query.SEND_EMAIL_OPC}`,
                        variables: {
                            input: {
                                type: this.soporte.servicio,
                                desc: text
                            }
                        }
                    });

                    if (nuevoCorreo.data.sendEmailAdd.state) {
                        await this.createAlert("Good!!!", nuevoCorreo.data.sendEmailAdd.message, "success").then(() => {
                            location.reload();
                        });
                    } else {
                        await this.createAlert("Oh oh!!!", nuevoCorreo.data.sendEmailAdd.message, "error");
                    }                    
                } catch(e) {
                    await this.createAlert("Oh oh!!!", "Algo raro paso por aqu!!!", "error");
                }
            } else {
                location.reload();
            }
        },

        async registrarSoporte() {
            if (this.$refs.form.validate()) {
                try {
                    this.isLoading = true;
                    
                    const nuevoSoporte = await this.$apollo.mutate({
                        mutation: gql`${query.NEW_SUPPORT}`,
                        variables: {
                            input: {
                                servicio: this.soporte.servicio,
                                tecnico: this.soporte.tecnico,
                                solicitante: this.soporte.correo + "@unbosque.edu.co",
                                perfil: this.soporte.perfil,
                                bloque: this.soporte.bloque,
                                salon: this.soporte.salon,
                                labor: this.soporte.labor,
                                cambio: this.soporte.cambio,
                                solucionado: this.soporte.solucionado
                            }
                        }
                    });

                    setInterval(() => {
                        this.isLoading = false;
                    }, 6000);
                    
                    if (nuevoSoporte.data.createSoporte.state) {
                        await this.createAlert("Good!!!", nuevoSoporte.data.createSoporte.message, "success").then(() => {
                            location.reload();
                        });
                    } else {
                        await this.createAlert("Oh oh!!!", nuevoSoporte.data.createSoporte.message, "error");
                    }
                } catch(e) {
                    await this.createAlert("Oh oh!!!", "Algo raro paso por aqu!!!", "error");
                }
            }
        },

        async createAlert(title, message, type) {
            try {
                const alert = await swal.fire(title, message, type);
                return alert;
            } catch (e) {
                console.log(e);
            }
        }
    }
}
</script>