<template>  
    <v-app class="grey lighten-4">
        <v-container fill-height justify-center>
            <v-layout row wrap align-center justify-center>
                <v-flex xl5 lg5 md5 sm12 xs12>
                    <loading :active="isLoading" :can-cancel="false" :is-full-page="true"></loading>
                    <div class="animated fadeIn">
                        <v-card raised>
                            <v-card-title class="headline light-green darken-1 white--text" primary-title>
                                <h4 class="font-weight-medium text-uppercase">Sing In</h4>
                            </v-card-title>
                            <v-card-text>
                                <v-form ref="form" @submit.prevent="logIn" autocomplete="off">
                                    <v-text-field prepend-icon="fa-user" label="Usuario" class="mx-5 my-4" v-model="login.usuario" :rules="rules.field" required></v-text-field>
                                    <v-text-field prepend-icon="fa-key" label="Password" type="password" class="mx-5 my-4" v-model="login.password" :rules="rules.field" required></v-text-field>
                                    <v-btn type="submit" class="orange lighten-1 text-white" block>Sing In</v-btn>
                                </v-form>
                                <v-alert :type="resLogin.typeLogin" class="mt-5 animated fadeIn" outlined text v-show="resLogin.isLogin">
                                    {{resLogin.textLogin}}
                                </v-alert>
                            </v-card-text>
                        </v-card>
                    </div>
                </v-flex>
            </v-layout>
        </v-container>
    </v-app>
</template>
<script>
    import Vue from 'vue';
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';
    import gql from "graphql-tag";
    import query from "../query/sesion";
    import VueSession from "vue-session";
    import getRules from "../validations/rules";

    Vue.use(Loading);
    Vue.use(VueSession);

    export default {
        components: {
            Loading
        },
        data() {
            return {
                login: {
                    usuario: "",
                    password: "",
                },
                isLoading: true,
                
                resLogin: {
                    isLogin: false,
                    typeLogin: "error",
                    textLogin: ""
                },
                rules: getRules.rulesIndex
            }
        },
        mounted() {
            setInterval(() => {
                this.isLoading = false;
            }, 1000);
        }, 
        methods:
        {
            async logIn() {
                if (this.$refs.form.validate()) {
                    this.isLoading = true;
                    try {
                        const session = await this.$apollo.query({
                            query: gql`${query.SESSION}`, 
                            variables: {
                                input: {
                                    usuario: this.login.usuario,
                                    password: this.login.password
                                }
                            }
                        });

                        setTimeout(() => {
                            this.isLoading = false;
                        }, 6000); 

                        this.resLogin.isLogin = true;
                        this.resLogin.typeLogin = session.data.login.resLogin;
                        this.resLogin.textLogin = session.data.login.message;

                        if (session.data.login.status) {
                            setInterval(() => {
                                this.$session.start();
                                window.localStorage.setItem("jwt", session.data.login.token);
                                this.$router.push("main");
                                
                            }, 3000);
                        } else {
                            this.resetForm();
                        }
                    } catch(e) {
                        setInterval(() => {
                            this.isLoading = false;
                        }, 6000);
                        
                        this.resLogin.isLogin = true;
                        this.resLogin.typeLogin = "error";
                        this.resLogin.textLogin = "Algo raro paso por aqu!!!";
                    }
                }
            }, 

            resetForm() {
                this.login.usuario = "";
                this.login.password = "";
                this.$refs.form.reset();
            }
        }
    }
</script>