const RULES = {
    rulesSoporte: {
        select: [
            v => !!v || "El campo es requerido"
        ],
        fieldSalon: [
            v => !!v || "El campo es requerido",
            v => (v.length > 2) || "Minimo 3 caracteres",
            v => (v.length < 5) || "Maximo 4 caracteres"
        ],
        fieldCorreo: [
            v => !!v || "El campo es requerido",
        ]
    },

    rulesIndex: {
        field: [
            v=> !!v || "El campo es requerido"
        ]
    },

    rulesMain: {
        select: [
            v => !!v || "El campo es requerido"
        ],
        date: [
            v => !!v || "El campo es requerido"
        ]
    }
}

export default RULES